import React from 'react';
import { useState } from 'react';
import Axios from 'axios';
import styled from 'styled-components'
import {Header,AppNameComponent,AppIcon,SearchComponent,SearchIcon,SearchInput} from './components/headerComponent.jsx'
import {RecipeListContainer,RecipeContainer,CoverImage,RecipeName,Ingredients,SeeMoreText } from './components/recipeComponent.jsx';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';

const Container =styled.div`
display:flex;
flex-direction:column;
`
const Placeholder = styled.img`
width:150px;
height:150px;
margin:200px;
opacity:50%;
`

const APP_ID="74af4d67";
const APP_KEY="743f6c03844668324cbec3b39ebbd05d";

const RecipeComponent = (props) =>{
  const [show, setShow] = React.useState(false);
  const {recipeObj}=props;
  return (
    <div>
      <Dialog open={show}>
      <DialogTitle id="alert-dialog-title">Ingredients</DialogTitle>
      <DialogContent>
        <table>
          <thead>
            <th>Ingredients</th>
            <th>Weights</th>
          </thead>
          <tbody>
            {recipeObj.ingredients.map((ingredientObj) => (
              <tr>
                <td>{ingredientObj.text}</td>
                <td>{ingredientObj.weight}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </DialogContent>
      <DialogActions>
      <Ingredients onClick={()=> window.open(recipeObj.url)}> See More </Ingredients>
      <SeeMoreText onClick={()=> setShow(false)}> Close </SeeMoreText>
      </DialogActions>
      </Dialog>
      <RecipeContainer>
      <CoverImage src={recipeObj.image} />
      <RecipeName>{recipeObj.label}</RecipeName>
      <Ingredients onClick={()=> setShow(true)}>Ingredients</Ingredients>
      <SeeMoreText onClick={()=> window.open(recipeObj.url)}>See Complete Recipe</SeeMoreText>
      </RecipeContainer>
      
    </div>
  )
}

 

function App() {
  const[timeoutId,updatetimeoutId]=useState();
  const[recipeList,updateRecipeList]=useState([]);
  const onTextChange = (event)=>{
    clearTimeout(timeoutId);
    const timeout = setTimeout(()=>fetchRecipe(event.target.value),500);
    updatetimeoutId(timeout);
  };

  const fetchRecipe =async (searchString)=>{
    const response = await Axios.get(`https://api.edamam.com/search?q=${searchString}&app_id=${APP_ID}&app_key=${APP_KEY}`) ;
    updateRecipeList(response.data.hits);
  }
  
  return (
    <Container>
      <Header>
      <AppNameComponent><AppIcon src="hamburger.svg" />Recipe Finder</AppNameComponent>
      <SearchComponent>
        <SearchIcon src="search-icon.svg" />
        <SearchInput placeholder="Search Recipe" onChange={onTextChange} />
      </SearchComponent>
      </Header>
      <RecipeListContainer>
      {recipeList.length ?
      recipeList.map((recipeObj)=>(
        <RecipeComponent recipeObj={recipeObj.recipe} />
      )): <Placeholder src="breakfast.svg"  />}
      </RecipeListContainer>
    </Container>
    
  );
}

export default App;
